  font-family: Arial, sans-serif;
  margin: 0;
  background-color: darkgrey;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #333;
  color: #fff;
  padding: 10px 20px;
}

.app-bar__brand a {
  text-decoration: none;
  color: #fff;
  font-size: 24px;
}

#menu {
  list-style: none;
  display: flex;
}

#menu li {
  margin: 0 10px;
}

#menu a {
  text-decoration: none;
  color: #fff;
  font-size: 16px;
}

.hero {
  border: 10px solid #fff;
}

@media (min-width: 1200px) {
  .hero {
    display: block;
    object-fit: cover;
    height: 400px;
    margin: auto;
    width: 100%;
    min-width: 1000px;
  }
}

@media (min-width : 480px) {
  #hamburger {
    display: none;
  }
}

@media (max-width : 480px) {
  ul#menu {
    display: none;
  }
  #hamburger {
    display: inline-block;
    min-width: 44px;
    min-height: 44px;
  }
}

@media (max-width: 1200px) {
  .hero {
    width: 100%;
    object-fit: cover;
  }
}

h3 {
  text-align: center;
}

.restaurant-item {
  border: 1px solid #ccc;
  padding: 10px;
  box-sizing: border-box;
  background-color: #fff;
}

.restaurant-item img {
  width: 100%;
  height: auto;
}

.restaurant-item h2 {
  font-size: 1.2em;
  margin: 10px 0;
}

.restaurant-item p {
  margin: 5px 0;
}

footer {
  bottom: 0;
  background-color: #333;
  color: #fff;
  text-align: center;
  padding: 10px;
  width: 100%;
}


nav {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #333;
  color: #fff;
  padding: 10px 20px;
}

.logo a {
  display: inline-block;
  padding: 4px;
  min-width: 44px;
  min-height: 44px;
  text-decoration: none;
  color: #fff;
  font-size: 24px;
}

